function AboutMe() {
  return (
    <StyledAboutMe id="aboutMe">
      <MyPhoto imageSrc={myDesktopPhoto} imageSrcMobile={myMobilePhoto} />
      <StyledAboutMeText>
         <StyledKeyword>Web3 Frontend </StyledKeyword>  
        .      {" "}
        <StyledKeyword>    , </StyledKeyword>
           <StyledKeyword>Leetcode</StyledKeyword>  {" "}
        <StyledKeyword>-.</StyledKeyword>     
          AI,  {" "}
        <StyledAboutMeLink href="https://www.youtube.com/@krashher_ai">
          YouTube
        </StyledAboutMeLink>{" "}
           :)
      </StyledAboutMeText>
    </StyledAboutMe>
  );
}

export default AboutMe;
