import React from "react";
import { StyledSkills } from "./styled";
import Title, { TitleColor, TitleLevel, TitleType } from "../../ui/title/title";
import SoftSkills from "./softSkills/softSkills";
import HardSkills from "./hardSkills/hardSkills";
import { myInstruments, myStack, mySoftSkillsList } from "./skills.config";

function Skills() {
  return (
    <StyledSkills id="skills">
      <Title
        level={TitleLevel.H2}
        color={TitleColor.PRIMARY}
        type={TitleType.SECTION}
      >
         
      </Title>
      <HardSkills title=" " stackList={myStack} />
      <HardSkills title="  " stackList={myInstruments} />
      <SoftSkills title=" " softSkillsList={mySoftSkillsList} />
    </StyledSkills>
  );
}

export default Skills;
